.TodoList li:nth-child(even) {
    background: rgba(252, 162, 79, 0.2);
}
.TodoList li:nth-child(odd) {
    background: rgba(252, 132, 211, 0.2);
}
.TodoItem{
    padding: .5em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
.TodoItem>.title{
    -ms-flex-positive: 1;
        flex-grow: 1;
    padding: 0 .5em;
}
.TodoItem button {
    background: transparent;
    border: none;
    cursor: pointer;
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
}
.TodoItem button:hover {
    color: rgba(252, 54, 59, 1)
}


